* {
  box-sizing: border-box;
}

body {
  margin: 16px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

input {
  padding: 8px;
  font: inherit;
}

button {
  padding: 8px 12px;
  cursor: pointer;
}
label {
  cursor: pointer;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.container {
  display: flex;
  margin: 100px 50px 50px 50px;
}
.box {
  width: 100px;
  height: 100px;
  margin: 30px;
}
.item-red {
  background-color: red;
}
.item-green {
  background-color: green;
}
.item-blue {
  background-color: blue;
}

.content {
  width: 152.2px;
  border: 1px solid black;
  margin: 0 auto;
}

.item {
  width: 50px;
  height: 50px;
  line-height: 50px;
  text-align: center;
  display: inline-block;
  font-size: 25px;
  border: 1px solid black;
  cursor: pointer;
  vertical-align: middle;
  transition: background 0.1s;
  user-select: none;
}

.item:hover {
  background-color: #f1f1f10c;
}

.section {
  padding-top: 40px;
  padding-bottom: 40px;
}

table {
  border-collapse: collapse;
}

th,
td {
  border: 1px solid black;
  padding: 10px 15px;
}